import Vue from 'vue'
import Vuex from 'vuex'
import { getToken } from '@/api/auth'
import getters from './getters.js'
import actions from './actions.js'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    token: getToken(),
    userInfo: {},
    menuFirst: [],
    menuSecond: [],
    indexAdminList: [],
    menuSecondW: 4,
    contentW: 20,
    gameListData: {},
    otherListData: {},
    reviewListData: {},
    configDomainUrl: {
      rootMainUrl: 'http://10.10.150.3:8035',//线上地址
      sendMainUrl: "http://10.10.150.3:8039",//内网测试地址
    },
    itemName: 'BMS',//项目名称
  },
  mutations: {
    SET_TOKEN: (state, token) => state.token = token,
    SET_USERINFO: (state, userInfo) => state.userInfo = userInfo,
    SET_MENUFIRST: (state, menuFirst) => state.menuFirst = menuFirst,
    SET_MENUSECOND: (state, menuSecond) => state.menuSecond = menuSecond,
    SET_INDEXADMINLIST: (state, indexAdminList) => state.indexAdminList = indexAdminList,
    SET_MENUSECONDW: (state) => {
      state.menuSecondW = state.menuSecondW === 2 ? 4 : 2;
      state.contentW = state.contentW === 22 ? 20 : 22;
    },
    SET_GAMELISTDATA: (state, gameListData) => state.gameListData = gameListData,
    SET_OTHERLISTDATA: (state, otherListData) => state.otherListData = otherListData,
    SET_REVIEWLISTDATA: (state, reviewListData) => state.reviewListData = reviewListData
  },
  getters,
  actions
})

export default store;
