import api from '@/api'
import { setToken } from '@/api/auth'
import iView from 'iview';

const actions = {
  LoginByUsername({ commit }, params) {
    return new Promise((resolve, reject) => {
      api.login(params).then(res => {
        const data = res.data;
        if(res.code === 0) {
          setToken(data.token)
          commit('SET_TOKEN', data.token)
          commit('SET_USERINFO', data.data)
          commit('SET_MENUFIRST', data.menu)
          resolve(res)
        } else {
          iView.Notice.error({
            title: res.msg
          });
        }
      }).catch(err => {
        reject(err)
      })
    })
  },
  GetIndexMenu({ commit,state }) {
    return new Promise((resolve, reject) => {
      const params = {'token': state.token,'need_menu': 1}
      api.indexMenu(params).then(res => {
        commit("SET_MENUFIRST", res.data.first)
        commit("SET_MENUSECOND", res.data.menu)
        commit("SET_INDEXADMINLIST", res.data.adminlist)
        resolve(res)
      }).catch(err => {
        reject(err)
      })
    })
  },
  GetAdMenu({ commit,state }) {
    return new Promise((resolve,reject) => {
      const params = {"token": state.token, "page": "1", "per_page": "10", "need_menu":1};
      api.advert(params).then(res => {
        commit("SET_MENUFIRST", res.data.first)
        commit("SET_MENUSECOND", res.data.menu)
        resolve(res)
      }).catch(err => {
        reject(err)
      })
    })
  },
  GetDataMenu({ commit,state }) {
    return new Promise((resolve,reject) => {
      const params = {"token": state.token, "page": "1", "per_page": "10", "need_menu":1};
      api.gameList(params).then(res => {
        commit("SET_MENUFIRST", res.data.first)
        commit("SET_MENUSECOND", res.data.menu)
        commit('SET_GAMELISTDATA', res.data)
        resolve(res)
      }).catch(err => {
        reject(err)
      })
    })
  },
  GetOtherMenu({ commit,state }) {
    return new Promise((resolve,reject) => {
      const params = {"token": state.token, "need_menu":1};
      api.apiothrtlist(params).then(res => {
        commit("SET_MENUFIRST", res.data.first)
        commit("SET_MENUSECOND", res.data.menu)
        commit('SET_OTHERLISTDATA', res.data.baidutui)
        resolve(res)
      }).catch(err => {
        reject(err)
      })
    })
  },
  GetREVIEWMenu({ commit,state }) {
    return new Promise((resolve,reject) => {
      const params = {"token": state.token, "need_menu":1};
      api.reviewGame(params).then(res => {
        commit("SET_MENUFIRST", res.data.first)
        commit("SET_MENUSECOND", res.data.menu)
        commit('SET_REVIEWLISTDATA', res.data)
        resolve(res)
      }).catch(err => {
        reject(err)
      })
    })
  },
}

export default actions;