import Vue from 'vue'
import App from '@/App.vue'
import router from '@/router'
import iView from 'iview'
import 'iview/dist/styles/iview.css'
import '@/icons'
import moment from 'moment'
import axios from 'axios'
import store from '@/store'
import { getToken } from '@/api/auth'

Vue.use(iView, axios)

Vue.prototype.$moment = moment

Vue.config.productionTip = false

router.beforeEach(async (to, from, next) => {
  const whiteList = ["/login"];
  const hasToken = getToken();
  if (hasToken && hasToken !== 'undefined') {
    if(to.path === '/login') {
      next({path: '/'})
    } else {
      const hasUserInfo = store.state.userInfo && store.state.userInfo.length > 0;
      if(!hasUserInfo) {
        const curFirstMenuPath = to.fullPath.split('/')[1];

        switch(curFirstMenuPath) {
          case 'admin': await store.dispatch('GetIndexMenu');
          break;
          case 'ad': await store.dispatch('GetAdMenu');
          break;
          case 'data': await store.dispatch('GetDataMenu');
          break;
          case 'othrtlist': await store.dispatch('GetOtherMenu');
          break;
          case 'review': await store.dispatch('GetREVIEWMenu');
          break;
        }
        // console.log(curFirstMenuPath)
        // console.log(to,from)
      }
      next()
    }
  } else {
    if (to.path === '/login' || whiteList.indexOf(to.path) !== -1) {
      next()
    } else {
      next('/login')
    }
  }
})

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')
